import { Injectable } from '@angular/core';
import { AngularFireDatabase } from '@angular/fire/database';
import * as _swal from 'sweetalert';
import { SweetAlert } from 'sweetalert/typings/core';
const swal: SweetAlert = _swal as any;
import { AngularFireStorage } from '@angular/fire/storage';
import { finalize } from 'rxjs/operators';

import { registroProductoBD } from '../../modelos/registrar-pruducto';

@Injectable({
  providedIn: 'root'
})
export class ProductosService {

  producto: registroProductoBD = new registroProductoBD();

  private basePath: string = '/Negocios';
  private productos: string = 'Productos';

  constructor(
    private firebase: AngularFireDatabase,
    private storage: AngularFireStorage
  ) {
  }  

  insertarProductoNegocio(
    registroProductoNF: registroProductoBD,
    idUsuario: string,
    keyNegocio: string,
    upload: any) {

    const filePath = `${this.basePath}/${idUsuario}/${keyNegocio}/${this.productos}/${registroProductoNF.nombre}/${upload[0].name}`;
    const storageRef = this.storage.ref(filePath);
    const uploadTask = this.storage.upload(filePath, upload[0]);

    uploadTask.snapshotChanges().pipe(
      finalize(() => {
        storageRef.getDownloadURL().subscribe(downloadURL => {
          return this.firebase.list('tienda_productos/' + keyNegocio).push({
            descripcion: registroProductoNF.descripcion,
            id_tienda: keyNegocio,
            imagen: downloadURL,
            nombre: registroProductoNF.nombre,
            precio: registroProductoNF.precio,
            tag: JSON.parse(localStorage.getItem('categoria_tienda')),
            uid_user_web: idUsuario
          }).then(
            (res) => {
              this.firebase.list('tienda_productos/' + keyNegocio).update(
                res.key ,{
                  id_producto: res.key,
                }
              ).then(
                () => this.subirFotosDelProducto(upload, idUsuario, keyNegocio, registroProductoNF.nombre, res.key)
              ).then(
                () => swal("Se ha registrado correctamente el producto", "", "success")
              );
            }
          );
        });
      })
    ).subscribe();
  }

  actualizarProductoNegocio(
    registroProductoNF: registroProductoBD,
    keyNegocio: string,
    idUsuario: string,
    upload?: any) {

    if (upload.length == 0) {
      return this.firebase.list('tienda_productos/' + keyNegocio).update(
        registroProductoNF.$key, {
          nombre: registroProductoNF.nombre,
          descripcion: registroProductoNF.descripcion,
          precio: registroProductoNF.precio
      }).then(
        () => {
          swal("El producto se ha actualizado correctamente", "", "success");
        }
      );
    } else {
      this.subirFotosDelProducto(upload, idUsuario, keyNegocio, registroProductoNF.nombre, registroProductoNF.$key);
    }
  }

  subirFotosDelProducto(
    upload: any,
    idUsuario: string,
    keyNegocio: string,
    nombreProducto: string,
    keyProducto: string) {
    
      for(let x = 0; x < upload.length; x++) {
                    
      const filePath = `${this.basePath}/${idUsuario}/${keyNegocio}/${this.productos}/${nombreProducto}/${upload[x].name}`;
      const storageRef = this.storage.ref(filePath);
      const uploadTask = this.storage.upload(filePath, upload[x]);

      uploadTask.snapshotChanges().pipe(
        finalize(() => {
            storageRef.getDownloadURL().subscribe(downloadURL => {
              this.firebase.list('producto_galeria/' + keyNegocio + '/' + keyProducto).push({
                foto: downloadURL,
              });
            });
          })
        ).subscribe();
    }

    this.firebase.list('producto_galeria/' + keyNegocio + '/' + keyProducto).set(
      'uid_user_web',{
        uid_user_web: idUsuario
    });
  }

  borrarProductoNegocio(keyProducto, keyNegocio) {    
    this.firebase.list('producto_galeria/' + keyNegocio + '/' + keyProducto.$key).snapshotChanges().subscribe(
      res => {
        res.forEach(element => {
          let x: any = element.payload.toJSON();
          x['$key'] = element.key;
          if (x.foto != undefined) {
            this.storage.storage.refFromURL(x.foto).delete();
          }
        });
      }
    );

    this.firebase.list('tienda_productos/' + keyNegocio + '/' + keyProducto.$key).remove().then(
      () => {
        this.firebase.list('producto_galeria/' + keyNegocio + '/' + keyProducto.$key).remove();
      }
    ).then(
      () => swal("El producto se ha eliminado exitosamente", "", "success")
    );
    
  }

}
